.App .App-Header {
  display: block;
  text-align: center;
}

.App .recharts-wrapper {
  margin: auto;
}

.content {
  padding: 10px 10%;
}

@media (max-width: 375px) {
  .content {
    padding: 10px 0;
  }
}

@media (min-width: 375px) and (max-width: 425px) {
  .content {
    padding: 10px 5%;
  }
}

.about {
  padding-left: 10%;
}

.footer {
  height: 80px;
}

.spacer {
  height: 40px;
}

.video-wrapper {
  width: 70%;
}

.video-container {
  height: 0;
  overflow: hidden;
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 767px) {
  .video-wrapper {
    width: 100%;
  }
}

